<template>
  <div>
    <transition-group name="fade" mode="in-out">
      <atom-spinner
            key="loading"
            v-show="loading"
            :animation-duration="1000"
            color="var(--accent)"
        />
      <div v-show="!loading" key="feedback">
        <h1>{{form_title}}</h1>
        <p>{{block_description}}</p>
        <form @submit.prevent="formHandler">
          <div class="field-box" v-for="field in block_fields" :key="field.model">
            <label :for="field.model">{{field.title}}</label>

            <input
              type="text"
              :id="field.model"
              :name="field.model"
              v-validate="field.validation"
              :placeholder="field.placeholder"
              v-model="models[field.model]"
              :class="{'valid':fieldsVV[field.model]&&fieldsVV[field.model].valid&&fieldsVV[field.model].touched && field.validation, 'invalid':fieldsVV[field.model]&&fieldsVV[field.model].invalid&&fieldsVV[field.model].touched && field.validation}"
              v-if="field.type === 'text'"
            />

            <textarea
              :id="field.model"
              :placeholder="field.placeholder"
              v-model="models[field.model]"
              :name="field.model"
              :class="{'valid':fieldsVV[field.model]&&fieldsVV[field.model].valid&&fieldsVV[field.model].touched && field.validation, 'invalid':fieldsVV[field.model]&&fieldsVV[field.model].invalid&&fieldsVV[field.model].touched && field.validation}"
              v-validate="field.validation"
              v-if="field.type === 'textarea'"
            ></textarea>

            <svg class="svg-invalid" width="16" height="16" viewBox="0 0 16 16">
              <path
                id="Forma_1"
                d="M1284,590a8,8,0,1,0,8,8A8,8,0,0,0,1284,590Zm4,10.868L1286.87,602l-2.87-2.868L1281.13,602l-1.13-1.132,2.87-2.868-2.87-2.868,1.13-1.132,2.87,2.868,2.87-2.868,1.13,1.132L1285.13,598Z"
                transform="translate(-1276 -590)"
              />
            </svg>
            <svg class="svg-valid" width="16" height="16" viewBox="0 0 16 16">
              <path
                id="Forma_1"
                d="M1284,494a8,8,0,1,0,8,8A8.01,8.01,0,0,0,1284,494Zm4.46,5.332-4.92,5.538a0.61,0.61,0,0,1-.46.207,0.631,0.631,0,0,1-.39-0.135l-3.07-2.461a0.613,0.613,0,1,1,.76-0.962l2.62,2.1,4.54-5.1A0.616,0.616,0,1,1,1288.46,499.332Z"
                transform="translate(-1276 -494)"
              />
            </svg>
            <span v-show="errors.first(field.model)" class="vv-error">{{ errors.first(field.model)}}</span>
          </div>
          <input v-if="sets && sets.code === 'send'" type="submit" class="btn btn-green" :value="sets.title" />
        </form>
      </div>
    </transition-group>
  </div>
</template>
<script>
import { AtomSpinner } from "epic-spinners";
export default {
  name: "Feedback",
  inject: ["$validator"],
  provide() {
    return {
      $validator: this.$validator
    };
  },
  data() {
    return {
      form_title: "",
      block_fields: [],
      models: [],
      block_description: "",
      loading: false,
      sets: {},
    };
  },

  methods: {
    async checkValidation() {
      let status = true;
      var validation = await this.$validator.validateAll();
      if (!validation) {
        for (var item in this.fieldsVV) {
          this.fieldsVV[item].touched = true;
        }
        let text = this.$store.getters.getLang === 'en' ? 'Incorrect data' : "Некорректные данные"

        this.$toast.error(text, "", { position: "topRight" });
        status = false;
      }
      return status;
    },
    async formHandler() {
      try {
        let validate = await this.checkValidation();
        if (!validate) return false;
        let result = await this.$http.post(
          "/bank/net/contact/send/email",
          this.models
        );
        await this.$store.dispatch("showToast", {
          message: result.data.message
        });
        Object.keys(this.models).forEach(e => {
          this.models[e] = "";
        });
      } catch (error) {
        this.$store.dispatch("showToast", {
          template: "error",
          title: error.response.status,
          message: error.response.data.message,
          position: error.response.data.position
        });
      }
    },
    async fillComponent() {
      this.loading = true;
      let res;
      res = await this.$http.post(this.$bRoute.meta.route);
      this.form_title = res.data.form_parameters.form_title;
      this.block_fields = res.data.tabs[0].blocks[0].block_fields;
      this.block_description = res.data.tabs[0].blocks[0].block_description;
      this.models = res.data.main_data_models[0];
      this.sets = res.data.sets;
      this.loading = false;
    }
  },
  mounted() {
    this.fillComponent();
  },
  components: {
      AtomSpinner
  }
};
</script>
<style lang="scss" scoped>
form {
  display: flex;
  flex-direction: column;
  input[type="submit"] {
    width: fit-content;
    margin-top: 1.5rem;
  }
  textarea {
    padding-left: 1.5rem;
    font-size: 1.5rem;
    padding-top: 0.5rem;
  }
  .field-box {
    position: relative;
    padding-bottom: 1.5rem;
    margin-bottom: 0;
    .vv-error {
      left: 0;
    }
  }
}
</style>